import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import { StaticQuery, graphql, withPrefix } from 'gatsby'
import { oneLine, stripIndent } from "common-tags"

import Box from 'components/Box';
import theme from 'components/ThemeProvider/theme';

import Header from '../Header'
import FloatButton from '../FloatButton';

const id = 'GTM-5GT6T9V'

const GTM = oneLine(stripIndent`
(function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({'gtm.start':
new Date().getTime(),event:'gtm.js'});var f=d.getElementsByTagName(s)[0],
j=d.createElement(s),dl=l!='dataLayer'?'&l='+l:'';j.async=true;j.src=
'https://www.googletagmanager.com/gtm.js?id='+i+dl;f.parentNode.insertBefore(j,f);
})(window,document,'script','dataLayer','${id}');`)

const GTMIframe = `<iframe src="https://www.googletagmanager.com/ns.html?id=${id}" height="0" width="0" style="display: none; visibility: hidden" aria-hidden="true"></iframe>`

const Layout = ({ children, noHeader }) => {
  const [loaded, setLoaded] = useState(true)
  // useEffect(() => {
  //   setTimeout(() => setLoaded(true), 2500)
  // }, [])
  return (
    <StaticQuery
      query={graphql`
        query SiteTitleQuery {
          site {
            siteMetadata {
              title
              description
              url
            }
          }
        }
      `}
      render={({ site: { siteMetadata: { title, description, url } } }) => {
        return (
          <>
            {loaded && (
              <noscript
                key="plugin-google-tagmanager"
                dangerouslySetInnerHTML={{ __html: GTMIframe }}
              />
            )}

            <Helmet>
              <html lang="zh-Hant-TW" />
              <title>{title}</title>
              <meta name="description" content={description} />
              <link rel="apple-touch-icon" sizes="180x180" href={withPrefix('/apple-touch-icon.png')} />
              <link rel="icon" type="image/png" sizes="32x32" href={withPrefix('/favicon-32x32.png')} />
              <link rel="icon" type="image/png" sizes="16x16" href={withPrefix('/favicon-16x16.png')} />
              <link rel="mask-icon" color="#5bbad" href={withPrefix('/safari-pinned-tab.svg')} />
              <meta name="msapplication-TileColor" content="#da532c" />
              <meta name="theme-color" content="#ffffff" />
              <meta property="og:url" content={url} />
              <meta property="og:title" content={title} />
              <meta property="og:description" content={description} />
              <meta property="og:image" content={`${url}/fb.png`} />
              {loaded && <script type="text/javascript">{GTM}</script>}
            </Helmet>
            {!noHeader && <Header height={theme.headerHeight} siteTitle={title} />}
            <Box as="main" pt={!noHeader && theme.headerHeight}>
              {children}
            </Box>
            <FloatButton />
          </>
        );
      }}
    />
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
