import React from 'react'
import { StaticImage } from 'gatsby-plugin-image';
import { Link as ScrollLink } from 'react-scroll'
import { get } from 'lodash';
import loadable from '@loadable/component'

import Box from 'components/Box';
import Flex from 'components/Flex';
import Text from 'components/Text';
import Link from 'components/Link';
import Button from 'components/Button';
import { Media, responsive } from '../components/ThemeProvider/theme';

import useCsvButton from './useCsvButton'

const Menu = loadable(() => import('./Menu'))

const links = [
  {
    name: '兩大焦點',
    to: 'section-focus'
  },
  {
    name: '永續三贏',
    to: 'section-subject'
  },
  {
    name: '基金檔案',
    to: 'section-fund'
  },
]

const offset = 48

const Header = ({ siteTitle, ...props }) => {
  const buttons = useCsvButton()
  // console.log(get(buttons, ['data'], []))
  return (
    <Flex
      as="header"
      position="fixed"
      bg="white"
      top={0}
      left={0}
      right={0}
      alignItems="center"
      justifyContent="space-between"
      zIndex="docked"
      {...props}
    >
      <Box.Relative width={responsive('11.25em', '14.625em')} mx={responsive('1em', '2em')}>
        <Text.H1>
          <Link to="/">
            <StaticImage src="logo.png" alt="logo" />
          </Link>
        </Text.H1>
      </Box.Relative>
      <Media greaterThanOrEqual="tablet">
        <Flex alignItems="center">
          {Boolean(get(buttons, ['data'], []).length) && buttons.data[0].name && (
            <Box mx="1.875em">
              <Button.Transparent
                color="black"
                fontSize="1em"
                fontWeight="normal"
                py="0"
                px="0"
              >
                {get(buttons, ['data'], []).filter(d => d.id === 'header-vid')[0]?.name}
              </Button.Transparent>
            </Box>
          )}
          {links.map(({ name, to }) => (
            <Box cursor="pointer" mx="1.875em">
              <ScrollLink smooth to={to} offset={-offset}>{name}</ScrollLink>
            </Box>
          ))}
          <Box mx="1.875em">
            <Button
              as={ScrollLink}
              cursor="pointer"
              smooth
              bg="white"
              to="section-buy"
              border="1px solid"
              color="custom.orange"
              fontSize="1em"
              fontWeight="normal"
              borderRadius="1.25em"
              _hover={{ bg: 'white' }}
              offset={-offset}
            >申購請洽</Button>
          </Box>
        </Flex>
      </Media>
      <Media lessThan="tablet">
        <Menu offset={offset} links={links} button={get(buttons, ['data'], []).filter(d => d.id === 'header-vid')} />
      </Media>
    </Flex>
  )
}

export default Header
